@charset "utf-8";
$color:#ff6e00;
/*!关闭按钮*/
#close{
    width: 100%;
    height: 40px;
    line-height: 40px;
    a{
        font-size: 24px;
        float: right;
        margin-right: 18px;
    }
    
}
/*!注册框*/
ul{
    width: 780px;
    height: 300px;
    margin: 50px auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    li{
        width: 625px;
        height: 55px;
        line-height: 55px;
        padding-left: 125px;
        font-size: 16px;
        text-align: left;
        display: flex;
        justify-content: start;
        align-items: center;
        span{
            display: inline-block;
            width: 100px;
            height: 55px;
            line-height: 55px;
            text-align: right;
        }
        input{
            outline: none;
            border: 1px solid black;
            width: 250px;
            height: 30px;
        }
        &:first-child{
            display: flex;
            justify-content: start;
            span{
                width:143px;
                height: 55px;
                display: block;
                background:no-repeat url(../img/taobaowang.png);
                background-size: contain;
            }
            p{
                width: 100px;
                height: 55px;
                margin-left: 5px;
                font-size: 25px;
            }
        }
        &:nth-child(4){
            display: none;
            span{
                width: 500px;
                line-height: 55px;
                text-align: center;
                border: 1px solid black;
            }
        }
    }
    /*!登录按钮*/
    div{
        width: 100px;
        height: 45px;
        line-height: 45px;
        text-align: center;
        font-size: 17px;
        background-color: $color;
        border-radius: 3px;
        color: white;
    }
}